import React from 'react';
import Helmet from 'react-helmet';
import Link from 'gatsby-link';
import Card from "../components/card"

import Hero from '../components/layout/Hero';
import Breadcrumbs from '../components/layout/Breadcrumbs';

export default function Template({data}) {
  const {linksJson: pages} = data;

  return (
    <div>
      <Hero title={pages.title} />
      <Breadcrumbs title={pages.title} />
      <div className="main">
        <div className="contain">
        <p className="lead">{pages.lead}</p>
          <ul>
            {pages.links.map((node, key) => (
              <li key={key}
                className={"col-md-6"+ 
                ((key+1)%2 == 0 ? '--last': '') + " col-lg-4" + 
                ((key+1)%3 == 0 ? '--last': '') + " button-" +key
              }>
                <Card
                card={node}
                />
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  )
};

export const pageQuery = graphql`
  query PageByPath($path: String!) {
    linksJson (page: {eq :$path}) {
      page
      title
      lead
      ...Card_details
    }
  }
`


