import React, {Component} from 'react';
import Link from 'gatsby-link';

import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import {faAngleRight} from '@fortawesome/fontawesome-pro-light';

class Breadcrumbs extends Component {

  constructor(props) {
    super(props)
  }

  render() {
    return (
      <div className="breadcrumbs">
        <div className="contain">
          <Link className="crumb" to="/">Cryptocurrency Resources</Link><span className="crumb"> <FontAwesomeIcon icon={faAngleRight} /> </span><span className="active-crumb crumb nowrap">{this.props.title}</span>
        </div>
      </div>
    )
  }
}

export default Breadcrumbs;



