import * as PropTypes from "prop-types"
import React from "react"
import Link from "gatsby-link"

class Card extends React.Component {
  static propTypes = {
    card: PropTypes.shape({
      title: PropTypes.string.isRequired,
      url: PropTypes.string.isRequired,
      description: PropTypes.string.isRequired,
      image: PropTypes.object,
    }).isRequired,
  }
  
  constructor() {
    super()
  }

  render() {
    const { title, url, description, image } = this.props.card
    const { small } = image.childImageSharp

    return (
      <a
      href={url}
      className="card"
      target="_blank" 
    >
      <div className="card-context">
      <img
        src={small.src}
        srcSet={small.srcSet}
        sizes="(min-width: 960px) 384px" 

        alt="" 
      />
      </div>

      <div className="card-lens">
        <h2>
          {title}
        </h2>
        <p>
          {description}
        </p>
      </div>

    </a>
    )
  }
}

export default Card

export const CardFragment = graphql`
  fragment Card_details on LinksJson {
    links {
      title
      url
      description
      image {
        childImageSharp {
          small: responsiveSizes(maxWidth: 384, maxHeight: 200) {
            src
            srcSet
          }
        }
      }
    }
  }
`



